}
/* 헤더 */
header { background-color:#cdcdcd; position:relative; }

/* 헤더 사이트명 */
header h1 { color:#fff; display:inline-block; font-size:20px; line-height:50px; margin-left:30px; vertical-align:top; }

/* 헤더 메뉴 버튼 */
header .btn-menu                 { position:absolute; right:10px; top:50%; transform:translateY(-50%); }
header .btn-menu a               { display:none; height:36px; text-align:center; vertical-align:middle; width:36px; }
header .btn-menu a svg           { display:none; fill:#fff; height:100%; width:100%; }
header .btn-menu a.hide svg.hide { display:inline-block; }
header .btn-menu a.show svg.show { display:inline-block; }

/* 
    헤더 1440px 이하 스타일
 */
@media (max-width:1440px) {
    /* 헤더 사이트명 */
    header h1 { font-size:2.083vw; line-height:5.556vw; margin-left:2.083vw; }

    /* 헤더 메뉴 버튼 */
    header .btn-menu                 { right:1.389vw; }
    header .btn-menu a               { display:inline-block; width:3.472vw; height:3.472vw; }
}

/* 
    헤더 1080px 이하 스타일
 */
@media (max-width:1080px) {
    /* 헤더 사이트명 */
    header h1 { font-size:5.556vw; line-height:14.815vw; margin-left:3.704vw; }

    /* 헤더 메뉴 버튼 */
    header .btn-menu                 { right:3.704vw; }
    header .btn-menu a               { display:inline-block; height:9.259vw; width:9.259vw; }
}
/* 콘텐츠 목록 아이템 */
.cardview-list article       { box-sizing:border-box; border:1px solid #cdcdcd; border-radius:5px; float:left; height:110px; margin-left:6px; margin-bottom:6px; overflow:hidden; position:relative; width:390px; }
.cardview-list article:hover { border:1px solid #999; }

/* 썸네일 */
.cardview-list article .thumbnail      { float:left; height:110px; margin-right:30px; overflow:hidden; position:relative; width:110px; }
.cardview-list article .thumbnail.hide { display:none; }
.cardview-list article .thumbnail img  { left:50%; position:absolute; top:50%; transform:translate(-50%, -50%); width:200%; }

/* 콘텐츠 목록 아이템 공통 */
.cardview-list article a    { display:block; height:100%; text-decoration:none; width:100%; }
.cardview-list article span { display:block; }

/* 썸네일 */
.cardview-list article .thumbnail     { float:left; height:110px; margin-right:30px; overflow:hidden; position:relative; width:110px; }
.cardview-list article .thumbnail img { left:50%; position:absolute; top:50%; transform:translate(-50%, -50%); width:200%; }

/* 제목, 사이트명, 등록일 등의 정보 영역 */
.cardview-list .data                    { padding:0 30px; position:relative; top:50%; transform:translateY(-50%); }
.cardview-list .data .title             { color:#555; font-size:18px; font-weight:bold; max-height:54px; overflow:hidden; }
.cardview-list .data span               { color:#cdcdcd; font-size:14px; }
.cardview-list .data span.site-target   { margin-top:14px; }
.cardview-list .data span.site-target i { margin-right:6px; }

@media ( max-width:1440px ) {
    /* 콘텐츠 목록 아이템 */
    .cardview-list article { height:13.889vw; margin-left:1.389vw; margin-bottom:1.389vw; width:47.917vw; }

    /* 썸네일 */
    .cardview-list article .thumbnail     { height:13.889vw; margin-right:1.389vw; width:13.889vw; }

    /* 제목, 사이트명, 등록일 등의 정보 영역 */
    .cardview-list .data                    { padding:0 1.389vw; }
    .cardview-list .data .title             { font-size:2.222vw; max-height:6.250vw; }
    .cardview-list .data span               { font-size:1.944vw; }
    .cardview-list .data span.site-target   { margin-top:0.972vw; }
    .cardview-list .data span.site-target i { margin-right:1.389vw; }
}

@media ( max-width:1080px ) {
    /* 콘텐츠 목록 아이템 */
    .cardview-list article { height:27.778vw; left:50%; margin-left:0; margin-bottom:1.852vw; transform:translateX(-50%); width:96.296vw; }

    /* 썸네일 */
    .cardview-list article .thumbnail     { float:left; height:27.778vw; margin-right:3.704vw; overflow:hidden; position:relative; width:27.778vw; }

    /* 제목, 사이트명, 등록일 등의 정보 영역 */
    .cardview-list .data                    { padding:0 3.704vw; position:relative; top:50%; transform:translateY(-50%); }
    .cardview-list .data .title             { color:#555; font-size:3.889vw; font-weight:bold; max-height:11.852vw; overflow:hidden; }
    .cardview-list .data span               { color:#cdcdcd; font-size:3.333vw; }
    .cardview-list .data span.site-target   { margin-top:1.296vw; }
    .cardview-list .data span.site-target i { margin-right:0.556vw; }
}

/* 콘텐츠 목록 */
.cardview-list { float:left; margin-top:20px; width:1190px; }

/* 다음 페이지 버튼 */
.cardview-list .btn-next                  { clear:both; padding:30px 0;  text-align:center; width:100%; }
.cardview-list .btn-next button           { background-color:#fff; border:0; display:block; height:40px; margin:0 auto; padding:0; width:40px; }
.cardview-list .btn-next button svg       { fill:#c4c4c4; height:100%; width:100%; }
.cardview-list .btn-next button:hover svg { fill:#b4b4b4; }

@media ( max-width:1440px ) {
    .cardview-list { float:none; margin-top:1.389vw; width:100%; }

    /* 다음 페이지 버튼 */
    .cardview-list .btn-next                  { padding:2.083vw 0; }
    .cardview-list .btn-next button           { height:4.167vw; width:4.167vw; }
}

@media ( max-width:1080px ) {
    /* 콘텐츠 목록 */
    .cardview-list { margin-top:1.852vw; }

    /* 다음 페이지 버튼 */
    .cardview-list .btn-next                  { clear:both; padding:2.778vw 0;  text-align:center; width:100%; }
    .cardview-list .btn-next button           { border:0; display:block; height:9.259vw; margin:0 auto; padding:0; width:9.259vw; }
}
/* 검색 */
.search { background-color:#cdcdcd; float:right; transform:translateX(0); padding:10px 0; margin-top:20px; width:230px; }

/* 검색창 및 검색 버튼 */
.search div        { height:24px; margin:0 auto; width:215px; }
.search div *      { border:0; display:inline-block; padding:0; vertical-align:middle; }
.search div input  { height:100%; width:170px; }
.search div button { background-color:#c4d4c4; color:#fff; font-size:14px; height:100%; padding:0; text-align:center; width:40px; }

/* 콘텐츠 타입 체크박스 */
.search ul          { margin:0 auto;  width:215px; }
.search ul li       { margin-top:10px; }
.search ul li *     { display:inline-block; vertical-align:middle; }
.search ul li input { border:1px solid #c4c4c4; height:20px; width:20px; }
.search ul li span  { color:#fff; font-size:24px; margin-left:4px; }

@media ( max-width:1440px ) {
     /* 검색 */
    .search { height:calc(100vh - 5.556vw); margin-top:0px; position:fixed; right:0; top:5.446vw; transition:transform 0.5s; transform:translateX(120%); width:300px; }
    .search.show { transform:translateX(0); }

    /* 검색창 및 검색 버튼 */
    .search div        { height:40px; width:265px; }
    .search div input  { width:190px; }
    .search div button { font-size:28px; width:70px; }

    /* 콘텐츠 타입 체크박스 */
    .search ul          { width:265px; }
    .search ul li       { margin-top:10px; }
    .search ul li input { /* height:40px; width:40px; */ }
    .search ul li span  { font-size:40px; margin-left:4px; }
}

@media ( max-width:1080px ) {
     /* 검색 */
    .search { height:calc(100vh - 14.815vw); margin-top:0px; position:fixed; right:0; top:14.500vw; transition:transform 1s; transform:translateX(120%); width:300px; }

    /* 콘텐츠 타입 체크박스 */
    .search ul li span  { font-size:30px; margin-left:4px; }
}
.contents-wrap { margin:0 auto; width:1440px; }

@media ( max-width:1440px ) {
    .contents-wrap { width:100%; }
}  
